<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Teacher',query:{typeName,index:0}}">教学大纲</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'TeacherOneDetail',query:{typeName,index}}">{{menuName}}</router-link>
    </div>
    <div class="content">
      <div class="myDetail">
        <div id="introduce" class="introduce">
          <div class="imgDiv">
            <img :src="file.imgUrl" alt="">
          </div>
          <p class="contentSmall">
            {{file.info}}
          </p>
          <div v-if="file.patent && file.patent.length > 0">
            <h2 class="titleSmall">
              授权发明专利：
            </h2>
            <p class="contentSmall" v-for="(item,index) in file.patent" :key="index">
              {{item}}
            </p>
          </div>
          <div v-if="file.project && file.project.length > 0">
            <h2 class="titleSmall">
              主持科研项目多项：
            </h2>
            <p class="contentSmall" v-for="(item,index) in file.project" :key="index">
              {{item}}
            </p>
          </div>
          <div v-if="file.parper && file.parper.length > 0">
            <h2 class="titleSmall">
              SCI论文50余篇，其中第一责任作者（通讯）30余篇。
            </h2>
            <p class="contentSmall" v-for="(item,index) in file.parper" :key="index">
              {{item}}
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import merge from 'webpack-merge'               //用于动态修改地址栏参数的
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        List:[],                 //评价数据数组
        menus:[],                //目录数组
        menuName:'',             //菜单名
        typeName:'Material',     //当前菜单栏选项
        index:0,                 //第几个小菜单

        file:{},                 //当前显示评价
      }
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //显示介绍
      setIntroduce(id,videoIntro){
        if(videoIntro){
          $('#'+id).html(videoIntro)
        }else{
          $('#'+id).html('此栏目下没有数据')
        }
      },

      //修改路由参数
      chanceUrlQuery(data){
        //清空所有的参数：
        this.$router.push({
          query:merge({},{})
        })
        //更新路由参数
        this.$router.push({
          query:merge(this.$route.query,data)
        })
      },

      //更新数据显示
      handleId(){
        var {List,menus,typeName,index} = this
        this.file = List[index]
        this.menuName = menus[index].menuName
        this.choiceMenuId(typeName)
        this.choiceTypeMenuId(typeName+index)
      },
    },
    watch: {
      //深度监听地址栏参数变化，一变化就切换对应的数据
      $route: {
        handler() {
          this.index = this.$route.query.index * 1
          this.handleId()
        },
        deep: true,
      }
    },
    mounted () {
      //页面重新加载时获取地址栏参数显示对应数据
      if(this.$route.query.typeName){
        this.typeName = this.$route.query.typeName
        this.index = this.$route.query.index * 1
      }else{
        var {typeName,index} = this
        this.chanceUrlQuery({typeName,index})
      }
      //当前栏目下的子菜单
      this.menus = [
        {
          typeName:'Teacher',
          menuName:'教师队伍结构',
          name:'TeacherDetail'
        },
        {
          typeName:'Teacher',
          menuName:'主讲教师',
          name:'TeacherOneDetail'
        },
      ],      //目录
      //当前栏目下的各个菜单包含的数据
      this.List = [
        {
          "id": "0",
          "imgUrl": "https://dss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3892521478,1695688217&fm=26&gp=0.jpg",
          "info":'赵志辉，男，1965年11月生，教授，理学博士，中国农业大学农业生物技术国家重点实验室生物化学与分子生物学专业博士后，瑞典Karolinska医学院分子免疫学博士后，教授，博士生导师，中国畜牧兽医学会动物遗传育种分会理事，动物遗传标记学会理事，动物生物技术学会理事，吉林省畜牧兽医学会理事，吉林省生物化学学会理事，吉林省动物繁殖改良学会副理事长。专业：动物遗传育种与繁殖；研究方向：（1）分子遗传与动物育种；（2）抗病育种。',
          "patent":[
            "(1) 杨润军，赵志辉，于海滨，孙博兴，芦春艳，张永宏，沈冰蕾，一种中国西门塔尔牛胴体和肉质性状遗传标记的检测方法，2014.9.24-2033.11.29，中国，ZL201310621249.X。",
            "(2) 龙小娟，芦春艳，赵志辉，杨润军，一种牛 AGPAT6 基因单核苷酸多态性的检测，2017.03.01-2036.03.10，中国，ZL201510195275.X。",
          ],
          "project":[
            "（1）.  高品质转基因奶牛新品种培育（2011ZX08007-001），国家科技重大专项，2011-2015，448.87万元；",
            "（2）.  中国肉牛重要经济性状功能基因组学研究（2013AA102505），国家“863”计划， 2013-2017，210万元；"
          ],
          "parper":[
            "[1]     Fang X, Zhao Z, Jiang P, et al. Identification of the bovine HSL gene expression profiles and its association with fatty acid composition and fat deposition traits[J]. Meat Science, 2017.（并列第一作者）",
            "[2]     Fang X, Zhao Z, Yu H, et al. Comparative genome-wide methylation analysis of longissimus dorsi muscles between Japanese black (Wagyu) and Chinese Red Steppes cattle[J]. PLOS ONE, 2017, 12(8): e0182492. （并列第一作者）",
          ]
        },
        {
          "id": "0",
          "imgUrl": "https://dss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3892521478,1695688217&fm=26&gp=0.jpg",
          "info":'赵志辉，男，1965年11月生，教授，理学博士，中国农业大学农业生物技术国家重点实验室生物化学与分子生物学专业博士后，瑞典Karolinska医学院分子免疫学博士后，教授，博士生导师，中国畜牧兽医学会动物遗传育种分会理事，动物遗传标记学会理事，动物生物技术学会理事，吉林省畜牧兽医学会理事，吉林省生物化学学会理事，吉林省动物繁殖改良学会副理事长。专业：动物遗传育种与繁殖；研究方向：（1）分子遗传与动物育种；（2）抗病育种。',
          "patent":[
            "(1) 杨润军，赵志辉，于海滨，孙博兴，芦春艳，张永宏，沈冰蕾，一种中国西门塔尔牛胴体和肉质性状遗传标记的检测方法，2014.9.24-2033.11.29，中国，ZL201310621249.X。",
            "(2) 龙小娟，芦春艳，赵志辉，杨润军，一种牛 AGPAT6 基因单核苷酸多态性的检测，2017.03.01-2036.03.10，中国，ZL201510195275.X。",
          ],
          "project":[
            "（1）.  高品质转基因奶牛新品种培育（2011ZX08007-001），国家科技重大专项，2011-2015，448.87万元；",
            "（2）.  中国肉牛重要经济性状功能基因组学研究（2013AA102505），国家“863”计划， 2013-2017，210万元；"
          ],
          "parper":[
            "[1]     Fang X, Zhao Z, Jiang P, et al. Identification of the bovine HSL gene expression profiles and its association with fatty acid composition and fat deposition traits[J]. Meat Science, 2017.（并列第一作者）",
            "[2]     Fang X, Zhao Z, Yu H, et al. Comparative genome-wide methylation analysis of longissimus dorsi muscles between Japanese black (Wagyu) and Chinese Red Steppes cattle[J]. PLOS ONE, 2017, 12(8): e0182492. （并列第一作者）",
          ]
        },
      ]
      //初始化数据
      this.handleId()
    },
  }
</script>

<style lang="less">
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        .myDetail{
          min-height: 470px;
          .introduce{
            color: #333;
            .imgDiv{
              margin: 20px auto;
              text-align: center;
              img{
                margin: auto;
                max-width: 400px;
                max-height: 400px;
              }
            }
            .titleSmall{
              font-weight: bold;
              font-size: 18px;
              margin: 20px 0 10px;
            }
            .contentSmall{
              text-indent: 1em;
              font-size: 14px;
              line-height: 24px;
              margin-bottom: 5px;
              .contentSmallSpan{
                display: block;
                text-indent: 2em;
                font-size: 14px;
                line-height: 24px;
              }
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
        .content{
          padding-top: 5px;
          .myDetail{
            .introduce {
              .titleBig {
                font-size: 20px;
              }
            }
          }
        }
      }
    }
  }
</style>



